@media screen and (max-width: 1600px) {
  /*header*/
  .main_head {
    .head_menu {
      max-width: none;
      position: absolute;
    }
  }
  /*END HEADER*/
}

@media screen and (max-width: 1500px) {
  .main_footer {
    .footer_head {
      padding: rem(100px) 0 rem(125px) 20%;
    }
  }
}

@media screen and(max-width: 1360px) {
  /* BEGIN HOME CONTENT*/
  .home_content {
    .content_menu {
      height: auto;
      margin-top: 0;

      .list_menu {
        flex-wrap: wrap;
        justify-content: center;

        .list_item {
          top: auto;
          &:first-child {
            flex: 1 1 45%;
            height: rem(300px);
            border-top: 0;
            border-bottom: 0;
            max-width: none;
            .item {
              width: 80%;
              height: 80%;
              a {
                width: 80%;
                bottom: 0;
              }
            }

          }
          &:nth-child(2) {
            flex: 1 1 45%;
            height: rem(300px);
            border-bottom: 0;
            max-width: none;
            .item {
              width: 80%;
              height: 80%;
              a {
                width: 80%;
                transform: rotate(0);
                left: 0;
                top: 0;
              }
            }
          }
          &:nth-child(3) {
            flex: 1 1 45%;
            height: rem(300px);
            border-top: 0;
            align-self: flex-start;
            max-width: none;
            .item {
              width: 80%;
              height: 80%;
              a {
                width: 80%;
                transform: rotate(0);
                left: 0;
                top: 0;
              }
            }
          }
          &:last-child {
            flex: 1 1 45%;
            height: rem(300px);
            border-top: 0;
            border-bottom: 0;
            align-self: flex-start;
            max-width: none;
            .item {
              width: 80%;
              height: 80%;
              a {
                width: 80%;
                left: 0;
                bottom: 0;
              }
            }

          }
          .item {
            margin: auto;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            display: flex;
            position: absolute;

            a {
              height: rem(80px);
              line-height: rem(76px);
              padding: 0 rem(40px);
              position: relative;
              margin: auto;

            }
          }
        }
      }
    }
  }
  /*END HOME CONTENT*/
}

@media screen and (max-width: 1280px) {
  .main_footer {
    .footer_head {
      padding: rem(30px) 0;
      align-items: center;
      flex-direction: column;

      .footer_contacts {
        order: 1;

        span {
          margin-bottom: 0;
        }
      }

      .footer_menu {
        margin-bottom: rem(30px);
        flex-direction: row;

        a {
          margin-left: rem(30px);
        }
      }

      .footer_logo {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .home_section {
    .slider {
      .slider_content {
        min-height: 0;
        .text_wrap {
          .slide_text {
            padding: rem(50px) rem(20px);
            text-align: center;
            .slide_title {
              font-size: rem(30px);
            }
          }
          .slider_control {
            left: auto;
            align-self: center;
            bottom: rem(50px);
          }
        }
      }
    }
  }
  /*about section*/
  .about_section{
    .about_content{
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 1000px) {
  // main head
  .main_head {
    .head_menu {
      padding: 0 rem(25px);
      display: flex;
      justify-content: space-between;

      .menu_nuv {
        position: absolute;
        right: 0;
        top: 70%;
        visibility: hidden;
        transition: 0.4s;
        opacity: 0;
        width: rem(200px);
        height: rem(150px);
        background-color: #000000;

        ul {
          display: flex;
          flex-direction: column;
          align-items: center;

          li {
            margin-left: 0;

            &::before {
              @include before_fig(100%, rem(2px), $blue_color);
              top: 25px;
            }
          }
        }

        &.active {
          top: 100%;
          opacity: 1;
          visibility: visible;
        }
      }

      .right_menu {
        flex-direction: row-reverse;

        .btn_mobile {
          margin-left: rem(20px);
          display: flex;

          &.active {
            .btn1 {
              transform: rotate(-411deg) translate(-15px, 1px);
            }

            .btn2 {
              opacity: 0;
            }

            .btn3 {
              transform: rotate(412deg) translate(-11px, 2px);
            }
          }
        }
      }
    }
  }
  // contacts_section
  .contacts_section {
    .right_menu {
      .feedback_form {
        width: 100%;
        padding: 0 rem(30px);

        h2 {
        }
      }
    }

    .left_menu {
      padding: rem(10px) rem(10px);

      .menu_item {
        margin-bottom: rem(10px);

        .menu_title {
          font-size: rem(17px);
        }

        .title_item {
          font-size: rem(14px);
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  /*home content*/
  .home_content {
    padding: rem(50 pxpx) 0 rem(20px);
    .content_menu {
      .list_menu {
        .list_item {
          .item {
            a {
              font-size: rem(20px);
            }
          }
        }
      }
    }
  }
  /*slider*/
  .home_section {
    .slider {
      .slider_content {
        min-height: 0;
        .text_wrap {
          .slide_text {
            padding: rem(30px) rem(20px);
            text-align: center;
            .slide_title {
              font-size: rem(25px);
            }
            .title_item {
              font-size: rem(13px);
            }
          }

        }
      }
    }
  }

}

@media screen and (max-width: 800px) {
  .feedback_section {
    .feedback_wrap {
      padding: rem(25px);
      flex-direction: column;

      .feedback_text {
        width: 100%;
        text-align: center;

        p {
          margin: rem(20px) 0;
        }
      }

      .feedback_form {
        width: 100%;
        max-width: none;
        text-align: center;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  /*slider*/
  .home_section {
    .slider {
      .slider_content {
        flex-direction: column;
        min-height: 0;
        .slide_img {
          width: 100%;
        }
        .text_wrap {
          width: 100%;
          position: static;
          .slide_text {
            width: 100%;
            padding: rem(30px) rem(20px);
            text-align: center;
            .slide_title {
              font-size: rem(25px);
            }
            .title_item {
              font-size: rem(13px);
            }
          }

        }
      }
    }
  }
}

@media screen and (max-width: 650px) {
  // contacts section
  .contacts_section {
    flex-wrap: wrap;

    .left_menu {
      order: 1;
      width: 100%;

      .menu_item {
        align-self: center;

        .title_item {
          width: 100%;
        }
      }
    }

    .right_menu {
      width: 100%;
      justify-content: center;
      padding: 20px 10px;

      .feedback_form {
        width: 100%;

        h2 {
          font-size: rem(30px);
        }

        input {
          margin-bottom: rem(30px);
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  /*header*/
  .main_head {
    .head_menu {
      .right_menu {
        .menu_language {
          margin-left: rem(15px);
          ul {
            li {
              font-size: rem(11px);
            }
          }
        }
      }
    }
  }
  /*footer*/
  .main_footer {
    .footer_head {
      .footer_menu {
        a {
          font-size: rem(15px);

          &:first-child {
            margin-left: 0;
          }
        }
      }
    }

    .footer_contacts {
      font-size: rem(15px);
    }
  }
  /*END FOOTER*/
  /*feedback*/
  .feedback_section {
    .feedback_wrap {
      padding: rem(5px);

      h2 {
        font-size: rem(25px);
      }

      p {
        font-size: rem(15px);
      }

      .feedback_form {
        padding: rem(50px);

        h2 {
          font-size: rem(30px);
        }
      }
    }
  }
  /*END FEEDBACK*/
  /*home content*/
  .home_content {
    .content_menu {
      .list_menu {
        flex-direction: column;
      }
    }
  }
  /*slider*/
  .home_section {
    .slider {
      .slider_content {
        .text_wrap {
          .slide_text {
            width: 100%;
            padding: rem(30px) rem(20px);
            text-align: center;
            .slide_title {
              font-size: rem(17px);
              margin-bottom: rem(10px);
            }
            .title_item {
              font-size: rem(13px);
            }
          }

        }
      }
    }
  }
}



